.video {
  background-color: red;
}
.section {
  display: inline-block;
  text-align: left;
  vertical-align: top;
  margin: 10px;
  width: 40%;
}

.player-wrapper {
  text-align: center;
  margin: 20px 0 0px auto;
  width: 100%;
  height: 100%;
  position: fixed;
  bottom: 10px;
  right: 10px;
}

.react-player {
  margin-bottom: 10px;
  background: rgba(0, 0, 0, 0.1);
  z-index: 0 !important;
}
.min {
  font-size: 10px;
}
.content-buttons .MuiButton-label {
  font-size: 10px;
}
.content-buttons {
  margin: 10px;
}

.hideme {
  z-index: 0 !important;
}

.top-options-left {
  z-index: 98 !important;
  position: absolute;
  top: 13px;
  left: 0;
  color: #ffff;
}
.top-options-right {
  z-index: 98 !important;
  position: absolute;
  top: 13px;
  right: 0;
  color: #ffff;
}

.options {
  z-index: 88 !important;
  color: #fff;
  opacity: 0.6;
  transition: all 0.3s ease;
  margin-top: -55px;
}

.options:hover {
  opacity: 1;
}

.options .MuiSlider-root {
  margin-bottom: -20px;
}

.options-group {
  display: flex;
  justify-content: space-between;
}

.options .MuiButton-root {
  background-color: #0001 !important;
  color: #fff;
  margin: 0 -1px;
}
.Selected {
  background-color: #3333 !important;
}

/* XXXXXXXXXXXXXXXXXXXX take care !!! XXXXXXXXXXXXXXXX */
.MuiMenu-list {
  padding: 0 !important;
}

.popContent .player-wrapper {
  margin: 0 -10px -10px 0;
  height: calc(100vh - 48px);
}