.fill {
  margin: 20px 10px;
  padding: 20px 5px;
  background-color: #fff;
  border-radius: 6px;
}

.fill .form {
  margin: 20px 0;
}

.addPlayList {
  margin: 60px 0;
}

.addVideo {
  margin: 20px 0;
  border: #aaa 1px solid;
  border-radius: 5px;
  background-color: #fff;
  padding: 10px;
}

.addVideo input[name="name"] {
  color: rgb(105, 0, 0) !important;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  font-weight: bold !important;
}

.addVideo input[name="url"] {
  color: rgb(18, 0, 121) !important;
  font-style: italic;
}

.fieldGroupCard {
  margin: 30px 0;
  border: 1px solid #ddd;
  padding: 20px 15px;
  border-radius: 5px;
  background-color: #f6f6f6;
}

.fieldGroupCard .MuiButtonBase-root {
  margin-top: 10px;
}
.addGoto {
  border: #ccc 1px solid;
  border-radius: 4px;
  background-color: #fafafa;
  padding: 15px;
  margin: 20px 0;
}

.playlistFields .MuiFormControl-root {
  margin: 5px 15px;
}

.formLoading {
    margin:10px 0 0 10px;
}
